var capnp = require('capnp-ts');

var partial = require('ap').partial;

var Log = require('../capnp/log.capnp');

var toJSON = require('@commaai/capnp-json');

var CapnpEvent = Log.Event;
module.exports = Event;

function Event(buf) {
  // force usage of new
  if (!(this instanceof Event)) {
    return new Event(buf);
  }

  this.msg = new capnp.Message(buf, false);
  this.event = this.msg.getRoot(CapnpEvent);
  this.toJSON = partial(toJSON, this.event);
}