var CapnpStream = require('capnp-split');

var EventWrapper = require('./event');

var Event = require('geval/event');

module.exports = streamReader;

function streamReader(inputStream, options) {
  options = options || {};
  var event = Event();
  var capnpStream = new CapnpStream();
  var isBinary = !!options.binary;
  var isStarted = false;
  capnpStream.on('message', function (buf) {
    if (!isBinary) {
      event.broadcast(new EventWrapper(buf).toJSON());
    } else {
      event.broadcast(buf);
    }
  });
  return pipeAndListen;

  function pipeAndListen(fn) {
    if (!isStarted) {
      isStarted = true;
      inputStream.pipe(capnpStream);
    }

    return event.listen(fn);
  }
}